.App {
  text-align: center;
}

.primary-background {
  background-color :#092340;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: calc(100vh + 30px);
}

.intro-container {
  height: inherit;
  position: relative;
  display: flex;
}


.intro-header {
  align-items: center;
  justify-content: center;
  font-size: calc(40px + 1.5vmin);
  position: relative;
}

.intro-sentence {
  position: relative;
  align-items: center;
  justify-content: center;
  font-size: calc(40px + 1.5vmin);
}

.no-bottom {
  margin-bottom: -100px;
}

.quick-info {
  position: relative;
  font-size: calc(20px + 2vmin);
}

.small-bottom {
  margin-bottom: -2%;
}


.learn-more-button {
  font-size: calc(20px + 2vmin);
  color: white;
  border-radius: 12px;
  border: none;
  margin-top: 2.5%;
  margin-bottom: 2.5%;
  animation: 1.5s infinite alternate ease-out breathing-color--dark;
}

@keyframes breathing-color--dark {
  from
  {
      background-color: #007db7;
  }
  to
  {
      background-color:#007db78e;
  }
} 

.profile-links {
  position: relative;
  justify-content: center;
}

.second-background {
  background-color: white;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vmax;
}